:root {
  --marks-translate-y: -17rem;
}


.timer-root {
  margin: auto;
}

.timer-wrapper-out {
  width: 28rem;
  height: 28rem;
  border-radius: 50%;
  padding: 2rem;
  border-width: 4px;
  background: linear-gradient(145deg, #cfcfcf, #f6f6f6);
  /* box-shadow:  41px 41px 82px #c6c6c6, 
               -41px -41px 82px #ffffff; */
  box-shadow: inset 10px 10px 30px #bbbbbb, inset -10px -10px 30px #ffffff;
}

.timer-wrapper-in {
  width: 28rem;
  height: 28rem;
  border-radius: 50%;
  box-shadow: 10px 10px 20px #aaaaaa, -10px -10px 20px #ffffff;
}

.marks {
  position:absolute;
  top:50%; left:50%;
  margin: 1.3em 0 0 0;
}

.marks li{
  display:block;
  width:.2em;
  height:.2em;
  border-radius: .2em;
  background:#bbbbbb;
  position:absolute;
  margin:-.1em 0 0 -.1em;
}

.marks .hours {
  background: #999999
}

.marks li:first-child {transform:rotate(6deg) translateY(var(--marks-translate-y))}
